import React, { useEffect } from "react";
import "./Strength.scss";
import useWindowSize from "utils/resizeViewport";
import ArrowRightAltIcon from "@material-ui/icons/ArrowRightAlt";
import Fab from "@material-ui/core/Fab";
import CardCom from "../card/Card";
import { default as Slider, CustomArrowProps, Settings } from "react-slick";
import { useLocalize } from "store/actions/localizeAction";
import { Link } from "react-router-dom";

const WOW = require("wowjs");

const NextArrow: React.SFC<CustomArrowProps> = (props) => {
  return (
    <div className={props.className} style={{ ...props.style, display: "block" }} onClick={props.onClick}>
      <Fab size="large">
        <ArrowRightAltIcon className="left" fontSize="large" />
      </Fab>
    </div>
  );
};

const PreviousArrow: React.SFC<CustomArrowProps> = (props) => {
  return (
    <div className={props.className} style={{ ...props.style, display: "block" }} onClick={props.onClick}>
      <Fab size="large">
        <ArrowRightAltIcon className="left" fontSize="large" />
      </Fab>
    </div>
  );
};
export interface StrengthProps {
  showArrow: boolean;
  imgContain?: boolean;
  keyData?: string;
  keyIndex: any;
}
export interface ButtonDataType {
  [key: string]: {
    text: string;
    href: string;
  };
}
export interface VisualDataType {
  mimeType: string;
  url: string;
}
export interface OffferDataType {
  actionName?: string;
  src?: string;
  line2: any;
  callback?: any;
  description: string;
  title: string;
  actionValue: string;
  visual: VisualDataType;
}
export interface LabelDataType {
  intro: string;
  title: string;
  description: string;
}
export interface StrengthDataType {
  button: ButtonDataType;
  label: LabelDataType;
  offers: OffferDataType[];
}

const Strength: React.FC<StrengthProps> = (props) => {
  useEffect(() => {
    const wow = new WOW.WOW({
      live: false,
    });
    wow.init();
  }, []);
  const [width] = useWindowSize();
  const getData = useLocalize<any>(props.keyData || "strengths");

  const data: StrengthDataType = Number.isInteger(props.keyIndex) && getData ? getData[props.keyIndex] : getData;

  //True - mobile
  const checkSizeDevice = () => {
    return width < 768;
  };

  const settings: Settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: checkSizeDevice() ? 1.15 : 3.15,
    slidesToScroll: 1,
    nextArrow: <NextArrow />,
    prevArrow: <PreviousArrow />,
    arrows: props.showArrow,
  };
  const renderHTML = require("react-render-html");

  const isShowIconSlider = () => {
    return !checkSizeDevice() && data && data.offers.length > 3;
  };

  const renderList = () => {
    return data?.offers.map((item, index) => {
      return (
        <CardCom
          key={index}
          img={item.visual.url}
          imgContain={props.imgContain === true}
          // title={item.title.slice(0, item.title.length - item.title.split(" ").splice(-1)[0].length)}
          // line2={item.title.split(" ").splice(-1)[0]}
          title={item.title}
          line2={item.line2}
          content={item.description}
          action={item.actionName}
          type={item.visual.mimeType}
          src={item.actionValue}
          callback={item.callback}
        />
      );
    });
  };

  return (
    <section className={`com-strength ${isShowIconSlider() ? "icon-slide" : ""}`}>
      <div className="label">{data?.label.title}</div>
      <div className="wrapper">
        {data?.label && <div className="title">{renderHTML(data.label.intro)}</div>}
        <div className="description">{data?.label.description}</div>
      </div>
      <Slider className="block-item" {...settings}>
        {renderList()}
      </Slider>
      {data?.button && (
        <div>
          <Link className="btn-offer" to="/offer">
            <div>{data?.button.discover.text}</div>
          </Link>
          <Link className="btn-offer-desk" to="/offer">
            <div>{data?.button.discover.text}</div>
          </Link>
        </div>
      )}
    </section>
  );
};

export default Strength;
