import { IBaseAction } from "store";
import { SpinnerAction } from "store/reducers/spinnerReducer";

export interface SpinnerAction_Hide extends IBaseAction {
  type: SpinnerAction.HIDE_SPINNER;
}

export interface SpinnerAction_Show extends IBaseAction {
    type: SpinnerAction.SHOW_SPINNER;
}
export type SpinneActionType = SpinnerAction_Show | SpinnerAction_Hide;

export const showLoading = () => {
  return {
    type: SpinnerAction.SHOW_SPINNER
  };
};

export const hideLoading = () => {
  return {
    type: SpinnerAction.HIDE_SPINNER
  };
};
