/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import Slider from "react-slick";
import CountUp from "react-countup";
import "slick-carousel/slick/slick-theme.css";
import "slick-carousel/slick/slick.css";
import useWindowSize from "utils/resizeViewport";
import "./Value.scss";
import { useLocalize } from "store/actions/localizeAction";
import { Link } from "react-router-dom";
import { LabelDataType } from "../strength/Strength";
import LazyImage from "libs/lazy-load/LazyImage";
export interface ValueProps {
  isMobile?: boolean;
  slideScroll?: number;
  keyData?: string;
  mode?: string;
}
export interface ValueDataType {
  button: {
    discover: {
      text: string;
      href: string;
    };
  };
  label: LabelDataType;
  figures: [
    {
      image: {
        url: string;
      };
      quote: string;
      value: string;
    }
  ];
}
const Value: React.FC<ValueProps> = (props) => {
  const [width] = useWindowSize();
  const data = useLocalize<ValueDataType>("values");

  //True - mobile
  const checkSizeDevice = () => {
    return width < 769;
  };
  const [render, setRender] = useState(false);
  const checkLength = () => {
    if (data) {
      return data.figures.length > 2 ? 3 : 2;
    }
    return 1;
  };

  const settings = {
    dots: true,
    customPaging: () => {
      return (
        <a href="#section">
          <div className="com-value-dots"></div>
        </a>
      );
    },
    infinite: true,
    speed: 500,
    slidesToShow: props.isMobile ? 1 : checkSizeDevice() ? 1 : checkLength(),
    slidesToScroll: props.slideScroll || 1,
    centerMode: props.slideScroll === undefined && true,
    centerPadding: "0px",
    arrows: false,
  };

  const trackScrolling = () => {
    const wrappedElement = document.getElementById("introduce") || document.getElementById("bestcase-detail");
    if (wrappedElement !== null && isBottom(wrappedElement)) {
      setRender(true);
      document.removeEventListener("scroll", trackScrolling);
    }
  };

  const isBottom = (el: HTMLElement) => {
    return el.getBoundingClientRect().bottom <= window.innerHeight / 1.25;
  };

  const countDecimals = (value: any) => {
    if (value && Math.floor(value) !== value) return value.toString().split(".")[1].length || 0;
    return 0;
  };

  const generate = () => {
    if (data) {
      return data.figures.map((slide, index) => {
        let str = slide.value;
        let myRegexp = /\d*\.?\d*/gm;
        let number = str.match(myRegexp)?.filter((x) => x !== "")[0] || "";
        let indexNumber = str.indexOf(number!);

        return (
          <div className="item" key={index}>
            <div className="wrapper-img">
              <LazyImage src={slide.image.url} alt="icon-slider"></LazyImage>
            </div>
            {/\d*\.?\d*/gm.test(str) ? (
              <div className="titleBig  title">
                {str.slice(0, indexNumber)}
                <CountUp end={parseFloat(number)} duration={3} separator="" decimal="." decimals={countDecimals(parseFloat(number))}></CountUp>
                {str.slice(indexNumber + number.length)}
              </div>
            ) : (
                <div className="titleBig spec title">{slide.value}</div>
              )}
            <div className="content">{slide.quote}</div>
          </div>
        );
      });
    }
  };

  useEffect(() => {
    document.addEventListener("scroll", trackScrolling);
    return () => {
      document.removeEventListener("scroll", trackScrolling);
    };
  }, []);

  return (
    <section className="com-value">
      {render && (
        <Slider className="block-item" {...settings}>
          {generate()}
        </Slider>
      )}

      {/* Button who we are page */}
      {data?.button && (
        <div className="wrapper-btnOffer">
          <Link className="btn-offer" to="/offer">
            <div>{data?.button.discover.text}</div>
          </Link>
          <Link className="btn-offer-desk" to="/offer">
            <div>{data?.button.discover.text}</div>
          </Link>
        </div>
      )}
    </section>
  );
};

export default Value;
