import React from "react";
import Slider from "react-slick";
import useWindowSize from "utils/resizeViewport";
import "./Brand.scss";
import { useLocalize } from "store/actions/localizeAction";
import LazyImage from "libs/lazy-load/LazyImage";
export interface LogoDataType {
  image: {
    url: string;
  };
  name: string;
}
export interface BrandDataType {
  label: {
    intro: string;
  };
  brands: LogoDataType[];
}

const Brand: React.FC = () => {
  // Page of slider show on mobile
  const PAGE_SIZE = 2;
  const [width] = useWindowSize();
  const data = useLocalize<BrandDataType>("brands");
  const checkSizeDevice = () => {
    return width < 768;
  };

  const settings = {
    dots: true,
    customPaging: () => {
      return (
        <a href="#section">
          <div className="com-value-dots"></div>
        </a>
      );
    },
    infinite: true,
    speed: 500,
    slidesToShow: checkSizeDevice() ? 1 : 2,
    slidesToScroll: 1,
    centerMode: true,
    centerPadding: "0px",
    arrows: false,
  };

  const renderSlider = () => {
    let arr = [];
    for (let index = 0; index < PAGE_SIZE; index++) {
      arr.push(
        <div className="item" key={index}>
          {renderBrand(data?.brands, index, data?.brands.length / 2)}
        </div>
      );
    }
    // Render
    return arr.map((item) => item);
  };

  const renderBrand = (arr: LogoDataType[], index: number, length: number) => {
    let arrActive = arr.slice(index * length, index * length + length);

    return arrActive.map((item: LogoDataType, index) => {
      return <LazyImage src={item.image.url} alt="logo" key={index} />;
    });
  };

  return (
    <div className="com-brand">
      <div className="title">{data?.label.intro}</div>
      {
        data?.brands && <Slider className="block-item" {...settings}>
          {renderSlider()}
        </Slider>
      }

    </div>
  );
};

export default Brand;
