import React from "react";
import Slider from "react-slick";
import useWindowSize from "utils/resizeViewport";
import "./OurGame.scss";
import { useLocalize } from "store/actions/localizeAction";
import LazyImage from "libs/lazy-load/LazyImage";
export interface GameImageDataType {
  title: string;
  image: {
    url: string;
  };
}

export interface OurGameDataType {
  label: {
    intro: string;
  };
  games: GameImageDataType[];
  button: {
    discover: {
      text: string;
    };
  };
}

const OurGame: React.FC = () => {
  const [width] = useWindowSize();
  //True - mobile
  const checkSizeDevice = () => {
    return width < 1280;
  };
  const data = useLocalize<OurGameDataType>("ourgames");

  const settings = {
    dots: true,
    customPaging: () => {
      return (
        <a href="#section">
          <div className="com-value-dots"></div>
        </a>
      );
    },
    infinite: true,
    speed: 500,
    slidesToShow: checkSizeDevice() ? 1 : 4,
    slidesToScroll: 1,
    centerMode: true,
    centerPadding: "0px",
    arrows: false,
  };

  const renderGame = () => {
    return data.games.map((item: GameImageDataType, index) => {
      return (
        <div className={`item ${item.title === "Despicable Me 2" ? "left" : ""}`} key={index}>
          <figure>
            <LazyImage src={item.image.url} alt="games" />
            <figcaption>{item.title}</figcaption>
          </figure>
        </div>
      );
    });
  };

  return (
    <div className="com-ourgame">
      <div className="title">{data?.label.intro}</div>
      {
        data?.games && <Slider className="block-item" {...settings}>
          {renderGame()}
        </Slider>
      }

      {data?.button && (
        <a href="https://www.gameloft.com/en/games" target="_blank" rel="noopener noreferrer">
          <div className="btn-ourgame">{data.button.discover.text}</div>
        </a>
      )}
    </div>
  );
};

export default OurGame;
