/* eslint-disable react/jsx-no-target-blank */
import React from "react";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import { styled } from "@material-ui/styles";
import "./Card.scss";
import LazyImage from "libs/lazy-load/LazyImage";
export interface Props {
  img: any;
  title: string;
  line2: string;
  content: string;
  src?: string;
  action?: string;
  type?: string;
  imgContain?: boolean;
  callback?: any;
}

const MyVisibility = styled(AddCircleIcon)({
  color: "#ff3050",
  marginRight: 5,
});

const CardCom: React.FC<Props> = (props) => {
  const videoRef = React.createRef<HTMLVideoElement>();
  const renderHTML = require("react-render-html");
  return (
    <div className="com-card">
      <div>
        {props.type && !props.type.includes("video") && <LazyImage className={props.imgContain ? "contain" : ""} src={props.img} alt="card" />}
        {props.type && props.type.includes("video") && (
          <div style={{ position: "relative", marginBottom: "1.65rem" }}>
            <video ref={videoRef} loop controls>
              <source src={props.img} type={props.type} />
            </video>
            {/* <img
              id="btnPlay"
              src={btnPlay}
              alt=""
              onClick={() => {
                videoRef.current?.play();
              }}
            /> */}
          </div>
        )}
      </div>
      <div className="title">{props.title}</div>
      <div className="title">{props.line2}</div>
      <div className="block-content">
        <div className={props.imgContain ? "large content" : " content"}>{renderHTML(props.content)}</div>
        {!props.callback && props.action && !props.src?.includes("http") && (
          <div className="action">
            <MyVisibility /> {props.action}
          </div>
        )}

        {!props.callback && props.action && props.src?.includes("http") && (
          <div className="action">
            <a target="_blank" href={props.src} style={{ display: "flex", alignItems: "center" }}>
              <MyVisibility />
              {props.action}
            </a>
          </div>
        )}
        {props.callback && props.action && (
          <div className="action">
            <div onClick={props.callback} style={{ display: "flex", alignItems: "center" }}>
              <MyVisibility />
              {props.action}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default CardCom;
